// =============================================================================
// VERCEL CRON ENDPOINT: Crawl Nexflow Actions
// =============================================================================
// GET /api/cron/crawl-actions
//
// Cron job to crawl all domains in the watchlist and update discovered actions.
// Called by Vercel Cron every 6 hours (schedule: "0 */6 * * *")
//
// Headers:
// - Authorization: Bearer <CRON_SECRET> (required in production)

import { NextRequest } from 'next/server';
import { createLogger } from '@/lib/logger';
import { withCronJobTracking, type CronJobContext } from '@/lib/cron-job-wrapper';
import type { NexflowAction, NexflowDiscoveredData } from '@/lib/nexflow-types';

const logger = createLogger({ component: 'CrawlActionsCron' });

// Watchlist of domains to crawl
const WATCHLIST_DOMAINS = [
  'https://www.nexflowapp.app',
  'https://discovery.nexflowapp.app',
];

// In-memory store (in production, use a database or KV store)
let cachedDiscovery: NexflowDiscoveredData = {
  crawledAt: new Date().toISOString(),
  actions: [],
};

// =============================================================================
// CRON HANDLER
// =============================================================================

async function crawlActionsHandler(
  _request: NextRequest,
  context: CronJobContext
): Promise<{ ok: boolean; [key: string]: any }> {
  const { runId, requestId } = context;

  logger.info({ runId, requestId, domains: WATCHLIST_DOMAINS, msg: 'Starting action crawl' });

  const allActions: NexflowAction[] = [];
  const results: { domain: string; status: string; actionCount?: number; error?: string }[] = [];

  for (const domain of WATCHLIST_DOMAINS) {
    try {
      const manifestUrl = `${domain}/.well-known/nexflow.json`;
      const res = await fetch(manifestUrl, {
        headers: { 'User-Agent': 'NexflowCrawler/1.0' },
        cache: 'no-store',
      });

      if (!res.ok) {
        results.push({ domain, status: 'error', error: `HTTP ${res.status}` });
        logger.warn({ runId, requestId, domain, status: res.status }, 'Domain returned error');
        continue;
      }

      const manifest = await res.json();
      const actions = (manifest.actions ?? []) as NexflowAction[];

      for (const action of actions) {
        allActions.push({
          ...action,
          domain,
        });
      }

      results.push({ domain, status: 'ok', actionCount: actions.length });
      logger.info({ runId, requestId, domain, actionCount: actions.length }, 'Domain crawled successfully');
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : 'Unknown error';
      results.push({
        domain,
        status: 'error',
        error: errorMessage,
      });
      logger.error({ runId, requestId, domain, error: errorMessage }, 'Failed to crawl domain');
    }
  }

  // Update cache
  cachedDiscovery = {
    crawledAt: new Date().toISOString(),
    actions: allActions,
  };

  const hasErrors = results.some(r => r.status === 'error');

  logger.info({
    runId,
    requestId,
    totalActions: allActions.length,
    successCount: results.filter(r => r.status === 'ok').length,
    errorCount: results.filter(r => r.status === 'error').length,
    msg: 'Action crawl completed',
  });

  return {
    ok: !hasErrors,
    crawledAt: cachedDiscovery.crawledAt,
    totalActions: allActions.length,
    results,
  };
}

// =============================================================================
// EXPORTS
// =============================================================================

export const dynamic = 'force-dynamic';
export const maxDuration = 60;

export const GET = withCronJobTracking(crawlActionsHandler, {
  jobId: 'crawl-actions',
  timeout: 55000, // 55s (leave buffer for wrapper overhead)
  minInterval: 5 * 60 * 60 * 1000, // 5 hours (schedule is every 6 hours)
});

/**
 * Export cached discovery for use by other routes
 */
export function getCachedDiscovery(): NexflowDiscoveredData {
  return cachedDiscovery;
}
